import {
  Drawer as BaseDrawer,
  Box,
  BoxProps,
  Button,
  Divider,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerContentProps,
  DrawerFooter,
  DrawerHeader,
  DrawerOverlay,
  DrawerProps
} from '@chakra-ui/react'
import { useMemo } from 'react'

interface IDrawerProps extends DrawerProps {
  title?: React.ReactNode
  footer?: React.ReactNode | boolean
  cancelText?: string
  okText?: string
  onSubmit?: () => void
  wrapStyle?: DrawerContentProps
  titleStyle?: BoxProps
}

export default function Drawer({
  title,
  isOpen,
  onClose,
  children,
  cancelText,
  okText,
  onSubmit,
  footer = false,
  placement = 'right',
  size,
  wrapStyle = {},
  titleStyle = {}
}: IDrawerProps) {
  const isFullHeight = useMemo(() => {
    return ['left', 'right'].includes(placement)
  }, [placement])
  return (
    <BaseDrawer isOpen={isOpen} placement={placement} onClose={onClose} size={size || 'md'}>
      <DrawerOverlay />
      <DrawerContent
        sx={
          isFullHeight
            ? {
                '@supports (height: 100dvh)': {
                  h: '100dvh !important'
                },
                '@supports not (height: 100dvh)': {
                  h: '100vh !important'
                },
                minH: 'calc(100vh - 80px) !important'
              }
            : {}
        }
        borderRadius={isFullHeight ? '0px' : '16px 16px 0 0'}
        {...wrapStyle}
      >
        {title && (
          <>
            <DrawerCloseButton />
            <Box p={isFullHeight ? '16px' : '0 16px'} {...titleStyle}>
              <DrawerHeader p="0">{title}</DrawerHeader>
              {!isFullHeight && <Divider orientation="horizontal" />}
            </Box>
          </>
        )}

        <DrawerBody>{children}</DrawerBody>

        <DrawerFooter>
          {typeof footer !== 'boolean'
            ? footer
            : footer && (
                <>
                  <Button variant="outline" mr={3} onClick={onClose}>
                    {cancelText}
                  </Button>
                  <Button colorScheme="blue" onClick={onSubmit}>
                    {okText}
                  </Button>
                </>
              )}
        </DrawerFooter>
      </DrawerContent>
    </BaseDrawer>
  )
}
